import React, { Fragment } from 'react';

import Link from './Link';

export default ({ list = [] }) => (
  <Fragment>
    Categories:
    <ul>
      {list.map(category => (
        <li key={category}>
          <Link to={`/categories/${category}`}>{category}</Link>
        </li>
      ))}
    </ul>
  </Fragment>
);
