import React from 'react';
import styled from 'styled-components';
import { graphql } from 'gatsby';
import MDXRenderer from 'gatsby-mdx/mdx-renderer';

import Layout from '../components/Layout';
import Link from '../components/Link';
import Image from '../components/Image';
import CategoryList from '../components/CategoryList';

const Container = styled.div`
  max-width: 750px;
  display: flex;
  flex-direction: column;
`;

export default function Post({
  data: { site, mdx },
  pageContext: { next, prev },
}) {
  return (
    <Layout site={site} frontmatter={mdx.frontmatter}>
      <Container>
        {/* Header */}
        <h1>{mdx.frontmatter.title}</h1>
        <h2>{mdx.frontmatter.date}</h2>

        {/* Banner Image */}
        {mdx.frontmatter.banner && (
          <Image
            sizes={mdx.frontmatter.banner.childImageSharp.sizes}
            alt={site.siteMetadata.keywords.join(', ')}
          />
        )}

        {/* Content */}
        <MDXRenderer>{mdx.code.body}</MDXRenderer>

        {/* Footer */}
        <div>
          <CategoryList list={mdx.frontmatter.categories} />

          <hr />

          {prev && (
            <span>
              Previous{' '}
              <Link to={prev.fields.slug}>{prev.fields.title}</Link>
            </span>
          )}
          {next && (
            <span>
              Next{' '}
              <Link to={next.fields.slug}>{next.fields.title}</Link>
            </span>
          )}
        </div>
      </Container>
    </Layout>
  );
}

export const pageQuery = graphql`
  query($id: String!) {
    site {
      ...site
    }
    mdx(fields: { id: { eq: $id } }) {
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        banner {
          childImageSharp {
            sizes(maxWidth: 900) {
              ...GatsbyImageSharpSizes
            }
          }
        }
        slug
        categories
        keywords
      }
      code {
        body
      }
    }
  }
`;
